body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --regular-text: "Average Sans";

  /* font sizes */
  --regular-text-size: 20px;
  --font-size-sm: 14px;
  --regular-text-underlined-size: 16px;
  --title-size: 24px;

  /* Colors */
  --input-field-background: #f2f4f8;
  --page-background: #e8eaee;
  --white: #fff;
  --black: #000;
  --secondary-gray: #999faa;

  /* Gaps */
  --gap-base: 16px;
  --gap-3xs: 10px;
  --gap-xl: 20px;
  --gap-9xs: 4px;
  --gap-5xs: 8px;
  --gap-xs: 12px;

  /* Paddings */
  --padding-5xl: 24px;
  --padding-5xs: 8px;
  --padding-xs: 12px;

  /* border radiuses */
  --br-xl: 20px;
  --br-xs: 12px;
  --br-9xs: 4px;
  --br-5xs: 8px;
  --br-base: 16px;
}
